@import url(https://fonts.googleapis.com/css?family=Kalam&display=swap);
body{margin:0;font-family:'Kalam', cursive;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;background-color:#cccbfd;background-image:-webkit-linear-gradient(60deg, #BAB9E6 20%, #CCCBFD 10%);max-height:100vh}.landing-img{width:80%;margin:5em;-webkit-transition:-webkit-transform .60s ease-in-out;transition:-webkit-transform .60s ease-in-out;transition:transform .60s ease-in-out;transition:transform .60s ease-in-out, -webkit-transform .60s ease-in-out}.landing-img:hover{-webkit-transform:rotate(-15deg);transform:rotate(-15deg)}.logo{width:80%;margin-top:8em}.col1{flex:50% 1;margin-top:3em;margin-left:5em;width:30%}.col2{flex:50% 1;margin-top:5em}.navbar{position:fixed;top:0;display:flex;left:0}.nav{position:fixed;margin:1%;width:5%;left:0;-webkit-transition:-webkit-transform .40s ease-in-out;transition:-webkit-transform .40s ease-in-out;transition:transform .40s ease-in-out;transition:transform .40s ease-in-out, -webkit-transform .40s ease-in-out}.nav:hover{-webkit-transform:rotate(-15deg);transform:rotate(-15deg)}.result{display:flex;flex-direction:row;justify-content:center;align-items:center}.result-tea{width:30%;-webkit-transition:-webkit-transform .60s ease-in-out;transition:-webkit-transform .60s ease-in-out;transition:transform .60s ease-in-out;transition:transform .60s ease-in-out, -webkit-transform .60s ease-in-out;display:flex;margin:0 3em}.result-tea:hover{-webkit-transform:rotate(-15deg);transform:rotate(-15deg)}.tea-description{flex:0.6 1;text-align:center}.form-container{height:100vh;display:flex;width:80%;margin:5em auto;background-color:rgba(255,255,255,0.64)}.result-container{display:block;width:80%;padding:3em;margin:5em auto 3em auto;background-color:rgba(255,255,255,0.64);max-height:100vh}

.container{position:relative;max-height:100vh}#content{position:absolute;max-height:100vh;width:100%;display:flex}h2{margin:0;padding-left:5%}a{text-decoration:none}

